// Ê†πÊçÆÂÆ†Áâ©ÂìÅÁßçÂà§Êñ≠Á±ªÂûã
const getPetType = (breed: string): string => {
  const breedLower = breed.toLowerCase();
  if (breedLower.includes('Áå´') || breedLower.includes('cat')) return 'cat';
  if (breedLower.includes('Áä¨') || breedLower.includes('Áãó') || breedLower.includes('dog')) return 'dog';
  if (breedLower.includes('È±º') || breedLower.includes('fish')) return 'fish';
  if (breedLower.includes('ÂÖî') || breedLower.includes('rabbit')) return 'rabbit';
  return 'other';
};

// ÁªüËÆ°ÂêÑÁ±ªÂûãÂÆ†Áâ©Êï∞Èáè
const getPetCountByType = (type: string): number => {
  return petRecords.filter(pet => getPetType(pet.breed) === type).length;
};

// Ê†πÊçÆÁ±ªÂûã ID Ëé∑ÂèñÂØπÂ∫îÁöÑ SVG ÂõæÊ†áË∑ØÂæÑ
const getPetTypeIcon = (typeId: string): string => {
  const iconMap: Record<string, string> = {
    'cat': '/images/svg/Â∏ÉÂÅ∂Áå´.svg',
    'dog': '/images/svg/ÂìàÂ£´Â•á.svg',
    'fish': '/images/svg/Âπ¥Âπ¥ÊúâÈ±º.svg',
    'rabbit': '/images/svg/ÂÖîÂ≠ê (1).svg',
    'other': '/images/svg/‰ªìÈº†.svg'
  };
  return iconMap[typeId] || iconMap['other'];
};

